body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
  font-size: 24px;  
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 72px;
  font-weight: bold;
  line-height: 100px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: center;  
  -ms-align-items: center;
  align-items: center;  
  height: 100vh;  
}
.game-info {
  margin-left: 20px;
}
.game-board {
  margin: 0 auto;
}
.restart {
  width: 200px;
  padding: 20px;
  margin: 20px auto;
  border: none;
  text-align: center;
  font-size: 24px;
  display: block;
  cursor: pointer;
  background-color: #ccc;
}
.restart:hover  {
  background-color: #cccfff;
}

@media screen and (max-width: 520px) {
  .square {
    height: 75px;
    width: 75px;
    font-size: 36px;
    line-height: 50px;
  }
}